.skills-container {
  display: grid;
  grid-template-columns: 100px 100px 100px;
  grid-template-rows: 100px 100px 100px;
  align-content: center;
  justify-content: center;
  height: 100%;
}

.skills-container > div {
  display: flex;
  align-items: center;
  justify-content: center;
}
